a,
a:link,
a:visited {
  color: #0299aa;
  font-weight: normal;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

p {
  margin: 0 0 10px 0;
  padding: 0;
}

blockquote {
  border: 1px solid #039fb1;
  border-left: 5px solid #000;
  padding: 19px;
  margin: 20px 0 0 0;
}

.tmo_list {
  margin: 10px 0 10px 15px;
  padding: 0;
  list-style: none;
}

.tmo_list li {
  color: #999;
  margin: 0 0 5px 0;
  padding: 0 0 0 15px;
  background: url(./../../../resources/img/templatemo_list.png) no-repeat scroll 0 5px;
}

h1,
h2,
h3 {
  color: #333;
  font-weight: normal;
}

h1 {
  font-size: 30px;
  margin: 0 0 30px;
  padding: 5px 0;
}

h2 {
  font-size: 26px;
  margin: 0 0 25px;
  padding: 5px 0;
}

h3 {
  font-size: 20px;
  margin: 0 0 20px;
  padding: 0;
}

.cleaner {
  clear: both;
}

.h20 {
  height: 20px;
}

.float_r {
  float: right;
}

#content {
  width: 690px;
}