body {
  margin: 0;
  padding: 0;
  color: #999;
  font-family: Tahoma, Geneva, sans-serif;
  font-size: 12px;
  line-height: 1.4em;
  background-color: #665f5f;
  background-repeat: repeat;
  background-image: url('./resources/img/templatemo_body.jpg');
}

a,
a:link,
a:visited {
  color: #0299aa;
  font-weight: normal;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

p {
  margin: 0 0 10px 0;
  padding: 0;
}

h1,
h3 {
  color: #333;
  font-weight: normal;
}

h1 {
  font-size: 30px;
  margin: 0 0 30px;
  padding: 5px 0;
}

h3 {
  font-size: 20px;
  margin: 0 0 20px;
  padding: 0;
}

.cleaner {
  clear: both;
}

.float_l {
  float: left;
}

.float_r {
  float: right;
}

#root {
  width: 100%;
  background: url(./resources/img/templatemo_body_top.jpg) repeat-x top;
}

#templatemo_wrapper {
  width: 980px;
  margin: 0 auto;
}

#templatemo_header {
  width: 960px;
  height: 70px;
  padding: 30px 10px;
  background: url(./resources/img/templatemo_header_bg.png) no-repeat bottom;
}

#templatemo_menubar {
  width: 980px;
  height: 60px;
  background: url(./resources/img/templatemo_menubar.png) no-repeat;
}

#templatemo_main {
  width: 940px;
  padding: 10px 20px;
  background: url(./resources/img/templatemo_main_bg.png) repeat-y;
}

#content {
  width: 690px;
}

#sidebar {
  width: 220px;
}

#templatemo_footer {
  width: 960px;
  padding: 30px 10px;
  text-align: center;
  background: url(./resources/img/templatemo_footer.png) no-repeat top;
}

#site_title {
  float: left;
  margin-top: 30px;
}

#site_title h1 {
  margin: 0;
  padding: 0;
}

#site_title h1 a {
  display: block;
  width: 300px;
  height: 40px;
  padding-top: 30px;
  font-size: 12px;
  letter-spacing: 4px;
  color: #2f2f2f;
  text-align: left;
  background: url(./resources/img/templatemo_logo.png) no-repeat top left;
}

#header_right {
  float: right;
  margin: 35px 300px 0 0;
  color: #666;
}

#header_right a {
  color: #fff;
}

#header_right p {
  text-align: right;
}

#templatemo_search {
  float: right;
  margin: 10px 30px 0;
  width: 220px;
  height: 34px;
  background: url(./resources/img/templatemo_search_bg.png);
}

#templatemo_search form {
  margin: 0;
  padding: 0;
}

#templatemo_search .txt_field {
  float: left;
  display: block;
  height: 22px;
  line-height: 22px;
  width: 170px;
  color: #999;
  font-size: 12px;
  padding: 5px;
  font-variant: normal;
  border: none;
  background: none;
}

#templatemo_search .sub_btn {
  float: right;
  display: block;
  color: #fff;
  height: 33px;
  width: 40px;
  font-size: 11px;
  font-weight: 700;
  line-height: 26px;
  cursor: pointer;
  border: none;
  background: none;
}

.sidebar_box {
  position: relative;
  background: #f0f0f0;
  margin-bottom: 36px;
  border: 1px solid #dbdbdb;
}

.sidebar_box h3 {
  font-size: 14px;
  font-weight: 700;
  padding: 0 10px 0 35px;
  margin: 0;
  width: 185px;
  height: 33px;
  line-height: 33px;
  margin-bottom: 0;
  background: url(./resources/img/templatemo_sidebar_header.png) no-repeat;
  color: #2f2f2f;
}

#sidebar .sidebar_box .content {
  padding: 10px 10px 0;
}

#sidebar .sidebar_list {
  margin: 0;
  padding: 0 0 10px;
  list-style: none;
}

#sidebar .sidebar_list li {
  display: block;
  margin: 0;
  padding: 5px 0;
  border-top: 1px solid #fff;
  border-bottom: 1px solid #d3d3d3;
}

#sidebar .sidebar_list li.first {
  border-top: none;
}

#sidebar .sidebar_list li.last {
  border-bottom: none;
}

#sidebar .sidebar_list li a {
  color: #666;
}

#sidebar .sidebar_list li a:hover {
  color: #000;
  text-decoration: none;
}

#templatemo_footer {
  color: #999;
}

#templatemo_footer a {
  color: #fff;
}

/*! CSS Used from: http://127.0.0.1:5501/css/ddsmoothmenu.css */
.ddsmoothmenu {
  float: left;
}

.ddsmoothmenu ul {
  z-index: 100;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.ddsmoothmenu ul li {
  position: relative;
  display: inline;
  float: left;
}

.ddsmoothmenu ul li a {
  display: block;
  padding: 0 30px;
  height: 40px;
  line-height: 40px;
  font-size: 12px;
  color: #2f2f2f;
  text-decoration: none;
  outline: none;
  text-align: center;
}

.ddsmoothmenu ul li a:hover {
  color: #fff;
  background: #2f2f2f;
}

.ddsmoothmenu ul li ul {
  position: absolute;
  left: 0;
  padding: 10px 0 0;
  display: none;
  visibility: hidden;
  background: #313131;
  border-bottom: 4px solid #000;
}

.ddsmoothmenu ul li ul li {
  display: list-item;
  float: none;
}

.ddsmoothmenu ul li ul li a {
  font-weight: 400;
  width: 130px;
  height: 30px;
  padding: 0 30px;
  line-height: 30px;
  font-size: 11px;
  text-align: left;
  background: none;
  color: #ccc;
  border-bottom: 1px solid #222;
  border-top: 1px solid #444;
}

.ddsmoothmenu ul li ul li a:hover {
  color: #fff;
  font-weight: 700;
  background: #222;
}

.ddshadow {
  position: absolute;
  left: 0;
  top: 0;
  width: 0;
  height: 0;
}

.toplevelshadow {
  opacity: 0.5;
}